import React from 'react';
import { CBadge } from '@coreui/react';

function getStatusBadgeColor(status) {
    switch (status) {
        case 'ACTIVE':
            return 'success';
        case 'DEACTIVATE':
            return 'secondary';
        case 'INACTIVE':
            return 'danger';
        default:
            return 'primary';
    }
}

function getStatusBadgeText(status) {
    switch (status) {
        case 'ACTIVE':
            return '';
        case 'DEACTIVATE':
            return '';
        case 'INACTIVE':
            return '';
        default:
            return '';
    }
}

function getDeleteBadgeColor(status) {
    switch (status) {
        case 'ACTIVE':
            return 'success';
        case 'DEACTIVATE':
            return 'secondary';
        case 'INACTIVE':
            return 'danger';
        default:
            return 'primary';
    }
}

function getDeleteBadgeText(status) {
    switch (status) {
        case 'ACTIVE':
            return '';
        case 'DEACTIVATE':
            return '';
        case 'INACTIVE':
            return '';
        default:
            return '';
    }
}

function getSnsIdBadgeColor(snsId) {
    switch (snsId) {
        case 0:
            return 'light';
        default:
            return 'warning';
    }
}

function getSnsIdBadgeText(snsId) {
    switch (snsId) {
        case 0:
            return '';
        default:
            return '';
    }
}

export function cellStatusBadge(status) {
    return <CBadge color={getStatusBadgeColor(status)}>{getStatusBadgeText(status)}</CBadge>;
}

export function cellDeleteBadge(status) {
    return <CBadge color={getDeleteBadgeColor(status)}>{getDeleteBadgeText(status)}</CBadge>;
}

export function cellSnsIdBadge(snsId) {
    return <CBadge color={getSnsIdBadgeColor(snsId)}>{getSnsIdBadgeText(snsId)}</CBadge>;
}
